.nav {
  position: fixed;
  top: 0;
  padding: 10px;
  width: 100%;
  height: 30px;
  transition-timing-function: ease-in;
  transition: all 0.5s;
  z-index: 1;
}
.nav__contents {
  display: flex;
  flex-direction: space-between;
}
.nav__black {
  background-color: #111;
}
.nav__logo {
  position: fixed;
  left: 0;
  width: 80px;
  object-fit: contain;
  padding-left: 20px;
  cursor: pointer;
  top: 10px;
}
.nav__avatar {
  cursor: pointer;
  position: fixed;
  right: 20px;
  width: 30px;
  height: 30px;
}
